
'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { getSessionUser, getEventsAction } from '@/lib/actions';
import type { User, Event } from '@/lib/types';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Loader2, AlertTriangle, Home, LayoutDashboard, Edit, Trash2, ListFilter } from 'lucide-react';
import { useLoading } from '@/contexts/loading-context';
import LoadingLink from '@/components/loading-link';
import { format, parseISO } from 'date-fns';
import { zhCN } from 'date-fns/locale';

export default function ManageEventsPage() {
  const router = useRouter();
  const { startLoading, stopLoading } = useLoading();
  
  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [isLoadingUser, setIsLoadingUser] = useState(true);
  const [events, setEvents] = useState<Event[]>([]);
  const [isLoadingEvents, setIsLoadingEvents] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchInitialData() {
      setIsLoadingUser(true);
      startLoading();
      try {
        const user = await getSessionUser();
        setCurrentUser(user);
        if (!user || user.op !== 1) {
          // Non-admins are handled in the render logic
          setIsLoadingUser(false);
          stopLoading();
          return;
        }
        // If admin, proceed to fetch events
        setIsLoadingEvents(true);
        const fetchedEvents = await getEventsAction();
        setEvents(fetchedEvents.sort((a, b) => parseISO(b.date).getTime() - parseISO(a.date).getTime()));
      } catch (err) {
        console.error("加载管理活动页面数据错误:", err);
        setError("加载数据时发生错误。");
      } finally {
        setIsLoadingUser(false);
        setIsLoadingEvents(false);
        // stopLoading will be called after events are potentially fetched
        // or if user is not admin.
        // Add a check to ensure stopLoading isn't called prematurely if still fetching events.
        if (typeof window !== 'undefined' && window.location.pathname === '/admin/manage-events' && !isLoadingEvents) {
            stopLoading();
        }
      }
    }
    fetchInitialData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []); // isLoadingEvents is managed internally

  const handleEditEvent = (eventId: string) => {
    startLoading();
    // For now, just log or redirect to a placeholder.
    // In a real app, this would navigate to an edit form.
    console.log(`尝试编辑活动: ${eventId}`);
    router.push(`/admin/edit-event/${eventId}`); // Placeholder route
  };
  
  const handleDeleteEvent = (eventId: string) => {
    startLoading();
    // Placeholder for delete functionality
    console.log(`尝试删除活动: ${eventId}`);
    alert(`模拟删除活动 ID: ${eventId} (此功能尚未实现)`);
    stopLoading(); // Call stopLoading as no navigation happens
  };

  if (isLoadingUser) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-15rem)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-4 text-muted-foreground">正在验证权限...</p>
      </div>
    );
  }

  if (!currentUser || currentUser.op !== 1) {
    return (
      <div className="max-w-2xl mx-auto py-8 text-center">
        <Card className="shadow-xl">
          <CardHeader>
            <CardTitle className="text-2xl font-bold">无权访问</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <Alert variant="destructive">
              <AlertTriangle className="h-4 w-4" />
              <AlertTitle>权限不足</AlertTitle>
              <AlertDescription>
                您没有管理活动的权限。请联系管理员。
              </AlertDescription>
            </Alert>
            <Button onClick={() => { startLoading(); router.push('/dashboard'); }} className="w-full">
              <LayoutDashboard className="mr-2 h-4 w-4" /> 返回仪表盘
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <Card className="shadow-xl">
        <CardHeader>
          <CardTitle className="text-3xl font-bold flex items-center">
            <ListFilter className="mr-3 h-7 w-7 text-primary" />
            管理活动
          </CardTitle>
          <CardDescription>查看、编辑或删除已创建的活动。</CardDescription>
        </CardHeader>
        <CardContent>
          {isLoadingEvents && (
            <div className="flex justify-center items-center py-10">
              <Loader2 className="h-10 w-10 animate-spin text-primary" />
              <p className="ml-3 text-muted-foreground">正在加载活动列表...</p>
            </div>
          )}
          {error && !isLoadingEvents && (
            <Alert variant="destructive">
              <AlertTriangle className="h-4 w-4" />
              <AlertTitle>加载错误</AlertTitle>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
          {!isLoadingEvents && !error && events.length === 0 && (
            <Alert>
              <AlertTriangle className="h-4 w-4" />
              <AlertTitle>暂无活动</AlertTitle>
              <AlertDescription>
                当前系统中没有任何活动。您可以 <LoadingLink href="/admin/create-event" className="font-medium text-primary hover:underline">创建一个新活动</LoadingLink>。
              </AlertDescription>
            </Alert>
          )}
          {!isLoadingEvents && !error && events.length > 0 && (
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>活动名称</TableHead>
                    <TableHead>日期</TableHead>
                    <TableHead className="text-center">参与者</TableHead>
                    <TableHead className="text-right">操作</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {events.map((event) => (
                    <TableRow key={event.id}>
                      <TableCell className="font-medium">{event.name}</TableCell>
                      <TableCell>{format(parseISO(event.date), 'yyyy-MM-dd HH:mm', { locale: zhCN })}</TableCell>
                      <TableCell className="text-center">
                        {event.participants.length} / {event.participantLimit || '无限制'}
                      </TableCell>
                      <TableCell className="text-right space-x-2">
                        <Button variant="outline" size="sm" onClick={() => handleEditEvent(event.id)}>
                          <Edit className="mr-1 h-3 w-3" /> 编辑
                        </Button>
                        <Button variant="destructive" size="sm" onClick={() => handleDeleteEvent(event.id)}>
                          <Trash2 className="mr-1 h-3 w-3" /> 删除
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          )}
        </CardContent>
      </Card>
       <Button variant="outline" onClick={() => {startLoading(); router.push('/dashboard')}} className="mt-6 w-full md:w-auto">
         <LayoutDashboard className="mr-2 h-4 w-4" /> 返回仪表盘
      </Button>
    </div>
  );
}

    